import React, { useContext, useMemo, useState } from 'react';
import { TwitchEmbed } from 'react-twitch-embed';
import { isNil } from 'lodash';
import { ethers } from 'ethers';
import { useQuery } from '@tanstack/react-query';
import { Header } from '../components/header/header';
import { PlaceBet } from '../components/place-bet/place-bet';
import { BetModal } from '../components/bet-modal';
import redCamel from '../../assets/images/red_camel.png';
import blueCamel from '../../assets/images/blue_camel.png';
import greenCamel from '../../assets/images/green_camel.png';
import orangeCamel from '../../assets/images/orange_camel.png';
import { WalletContext } from '../contexts/wallet-context';
import gameFacetAbi from '../../assets/contract-artifacts/game-facet-abi.json';

export const Home: React.FC = () => {
	const [selectedCamel, setSelectedCamel] = useState<string | null>(null);
	const { library } = useContext(WalletContext);

	const [nonce] = useState(Math.floor(Math.random() * 100_000));
	const gameContract = useMemo(
		() =>
			isNil(library)
				? null
				: new ethers.Contract(
					process.env.REACT_APP_CAMELS_CONTRACT_ADDRESS,
					gameFacetAbi,
					library
				),
		[library]
	);

	const { data: game, isLoading: isGameLoading } = useQuery(['game', isNil(gameContract)], async () => {
		if (gameContract) {
			const [gameId] = await gameContract.getLatestGameId(nonce);
			const [game] = await gameContract.getGame(gameId, nonce);
			return game;
		}
		return null;
	});

	const timeRemaining = useMemo(() => {
		if (isNil(game)) return null;
		const currentDate = new Date();
		return currentDate.getTime() - (game.beginTimestamp.toNumber() * 1000);
	}, [game]);

	console.log(timeRemaining);

	return (
		<div className="flex flex-col font-[Montserrat] h-screen">
			<BetModal
				open={!isNil(selectedCamel)}
				onClose={() => setSelectedCamel(null)}
				camelName={selectedCamel}
			/>
			<Header />
			<div className="flex flex-1 p-[20px] min-h-[600px]">
				{/* <div className="w-[185px] mr-[10px]">
          <Results />
        </div> */}
				<div className="w-full">
					<TwitchEmbed
						channel="camelsgg"
						autoplay
						withChat={false}
						width="100%"
						height="100%"
					/>
				</div>
			</div>
			<div className="flex flex-col w-full">
				<div className="flex rounded-lg bg-camel text-white text-lg p-[7px] mx-4 w-fit self-center mt-3">
					Place your bets, race starts in 01:00:00
				</div>
				<div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 text-white p-6 gap-4">
					<PlaceBet
						name="Frank"
						image={blueCamel}
						onPlaceBet={() => setSelectedCamel('Frank')}
					/>
					<PlaceBet
						name="Rocket"
						image={redCamel}
						onPlaceBet={() => setSelectedCamel('Rocket')}
					/>
					<PlaceBet
						name="Billy"
						image={greenCamel}
						onPlaceBet={() => setSelectedCamel('Billy')}
					/>
					<PlaceBet
						name="Toe"
						image={orangeCamel}
						onPlaceBet={() => setSelectedCamel('Toe')}
					/>
				</div>
			</div>
		</div>
	);
};
