import React, { useState } from 'react';
import { Popup } from '../popup';
import './style.css';

interface Props {
  open: boolean;
  onClose: () => void;
  camelName: string;
}

export const BetModal = ({ onClose, open, camelName }: Props) => {
	const [amount, setAmount] = useState(0);

	return (
		<Popup
			open={open}
			onClose={onClose}
			className="bet-modal font-[Montserrat]"
		>
			<div className="flex flex-col text-white bg-camel-light rounded-[10px] items-center p-4 w-full md:w-[500px]">
				<div className="text-4xl font-extrabold">NEW BET</div>
				<div className="text-2xl font-extrabold italic">{camelName}</div>
				<input
					className="w-full rounded-md text-xl font-extrabold px-3 py-4 bg-camel mt-2 focus:outline-none"
					value={amount}
					onChange={(e) => setAmount(parseInt(e.target.value, 10))}
					type="number"
				/>
				<button type="button" className="w-full py-2 rounded-md bg-[#c9795a] my-2 font-extrabold">
					PLACE BET
				</button>
				<button
					type="button"
					className="w-full py-2 rounded-md bg-camel font-extrabold"
					onClick={onClose}
				>
					CANCEL
				</button>
				<div className="text-[#c9795a] mt-2 font-extrabold">
					The return on your bet can change based on other camels wagers, and it
					may be adjusted up to 1 minute before the race begins.
				</div>
			</div>
		</Popup>
	);
};
